<template>
    <div>
      <input type="file" ref="fileInput" @change="uploadPhoto">
      <input type="button" @click="get_result" value="获取画框图片url及结果">
      <!-- <button @click="uploadPhoto">上传照片</button> -->
      
    </div>
    <div>
        <table style="margin-top:70px">
          <tr>
           {{this.img_data}}
          </tr>
        </table>

    </div>
</template>
  
  <script>
  import axios from 'axios'
  
  export default {
    name: 'PhotoUploader',
    data() {
      return {
        selectedFile: null,
        img_name:null,
        img_data:null,
        img_a :{
          "a":111
        }
      }
    },
    methods: {
      // handleFileInputChange(event) {
      //   console.log(event.target.files,'event')
      // },
      uploadPhoto(event) {
        // console.log(event.target.files[0],'event');
        this.selectedFile = event.target.files[0];
        console.log(this.selectedFile,'---');
        const formData = new FormData()
        formData.append('img', this.selectedFile)
  
        axios.post('api/v1/test_upload_img', formData, {
          headers: {
            'Content-Type': 'multipart/form-data',
            "Access-Control-Allow-Origin": "*"
          }
        }
        )
        .then(response => {
          if(response.data.code==200){
            alert("成功")
            this.img_name=response.data.img_name
            console.log(response.data)
          }else{
            alert("失败")
          }
          
        })
        .catch(error => {
          alert('上传失败')
          console.error(error)
        })
      },
      get_result(){
        axios.post('/api/v1/receive_result',{img_name:this.img_name})
        .then(response=>{
            if(response.data.code==200){
              this.img_data=response.data.data
              alert("ok")
            }else{
              "失败"
            }
        }).catch(error=>{
          console.error(error)
        })
        console.log(this.img_name)
      }
    }
  }
  </script>